<template>
<div>
	<div class="cover">
		<div-form
			:formFirst='formFirst'
			:levelList='levelList'
			:batchList='batchList'
			:departmentList='departmentList'
			:staffList='staffList'
			:val='val'
			v-on:changeFn='changeFn($event)'
			v-on:getDepartmentList='getDepartmentList($event)'
			:clientList='clientList'
		>
		</div-form>
		<div slot="footer" class="dialog-footer">
			<el-button type="primary" @click="searchFn" size="mini"></el-button>
			<el-button type="info" @click="searchFn" size="mini"></el-button>
		</div>
    <div> 
    <div class="blueB">
		<second-form
			:form='form'
			
		>
		</second-form>
	</div>
	</div>
    <div class="tableCover">
		<div class="totalT">
				{{totalNum}}{{titalAmount}}
		</div>
		<el-table
			border
			:data="tableData"
			style="width: 98%"
			@selection-change="handleSelectionChange"
			>
			<el-table-column
			label=""
			type="selection"
			width="55">
			</el-table-column>
			<el-table-column
			label="id"
			prop="id">
			</el-table-column>
			<el-table-column
			label=""
			prop="id">
			</el-table-column>
			<el-table-column
			label=""
			prop="id">
			</el-table-column>
			<el-table-column
			prop="name"
			label=""
			>
			</el-table-column>
			<el-table-column
			label=""
			prop="id">
			</el-table-column>
			<el-table-column
			label=""
			prop="id">
			</el-table-column>
			<el-table-column
			label=""
			prop="id">
			</el-table-column>
			<el-table-column
			label=""
			prop="id">
			</el-table-column>
			<el-table-column
			label=""
			prop="id">
			</el-table-column>
			<el-table-column
			label=""
			prop="id">
			</el-table-column>
			<el-table-column
			label=""
			prop="id">
			</el-table-column>
		</el-table>
		<page-change 
			:total="total"
			:page="page"
			:page_size ="page_size"
			v-on:pageSizeChange='pageSizeChangeFn($event)'
			v-on:currentPageChange='currentPageChangeFn($event)'
		></page-change>
    </div>
    <el-dialog :title="bannerTitle" :visible.sync="addNow" >
        <edit-dialog
            v-on:addNowChange = "addFn($event)"
            v-on:saveFn = "init($event)"
            v-on:clearId = "changeId($event)"
            v-on:clearFormTitle = "clearFormTitle($event)"
            :id="id"
            :title = "bannerTitle"
            :formTitle = "formTitle"
        ></edit-dialog>
    </el-dialog>
  </div>
</div>
 
</template>
<script>
import '@/assets/css/system.css'
import addImport from '@/functions/editDialog/addArea.vue'
import pageChange from '@/components/pageChange.vue'
import formCaseFirst from '@/functions/formCollection/formCaseFirst.vue'
import formCaseSecond from '@/functions/formCollection/formCaseSecond.vue'

import  { axiosRequest } from '@/assets/js/Yt.js'
import { Message } from 'element-ui'
import store from '@/vuex/store.js'
import router from '@/router.js'
// import indexMethod  from '@/utils/indexMethod.js'
export default {
    components:{
        'edit-dialog':addImport,
		'page-change':pageChange,
		'div-form':formCaseFirst,
		'second-form':formCaseSecond
	},
	data() {
        return {
			totalNum:'',//
			titalAmount:'',//
			formKey:{
				key:''
			},
			form: {
          		name: '',
				region: ''
			},
			levelList:[],//
			batchList:[],//
			departmentList:[],//
			clientList:[],//
			formFirst:{//
				case_name:'',
				case_mobile:'',
				case_id_num:'',
				case_status:'',
				case_client:'',
				case_date:'',
				case_back_date:'',
				case_money:'',
				id:'',
				case_last_collection_date:''
			},
			formInline: {
				user: '',
				region: ''
			},
            id:'',//
            page:1,
            page_size:10,
            total:0,
            bannerTitle:"",
            addNow:false,
            tableData: [{
                miniImage: ''
            }, {
                miniImage: ''
            }],
            formTitle:{//
                name:''
            },
        }
    },
    created() {
		// this.$route.params&&this.$route.params.staff_id
		// console.log(route.params&&route.params.staff_id)
		this.init()
		console.log(store)
		this.getBatchList()
		this.getDepartmentList(1)
	},
	methods: {
		handleSelectionChange(val){
			console.log(val)
		},
		searchFn(){

		},
        onSubmit(){

		},
		getBatchList(){
			let conf = {
                url : '/api/api_backend.php?r=collection/init-search',
                success:(data)=>{
					if( data.statusCode == 1 ){
						this.levelList = data.info.case_level
                        this.batchList = data.info.batch_id
                    }
                }
            }
            axiosRequest(conf)
		},
		getDepartmentList(){
			let conf = {
                url : '/api/api_backend.php?r=collection/depart-search',
                success:(data)=>{
					if( data.statusCode == 1 ){
						this.departmentList = data.info.depart
                    }
                }
            }
            axiosRequest(conf)
		},
		getClientList(){
			let conf = {
                url : '/api/api_backend.php?r=collection/client-search',
                success:(data)=>{
					if( data.statusCode == 1 ){
						this.clientList = data.info
                    }
                }
            }
            axiosRequest(conf)
		},
        init(){
            let conf = {
                url : '/api/api_backend.php?r=system-setting/area-list',
                data : {
                    page:this.page,
                    page_size:this.page_size
                },
                success:(data)=>{
                    if( data.statusCode == 1 ){
                        this.tableData = data.info
                        this.formTitle
                        this.total = Number( data.total )
                    }
                }
            }
            axiosRequest(conf)
        },
		addFn(val){//
            this.bannerTitle = ""
            this.addNow = val
            this.id = ''
        },
        changeId(){//
            this.id = ''
        },
        clearFormTitle(){//
            this.formTitle = {
                name:''
            }
        },
        editFn(row){//
            this.bannerTitle = ""
            this.id = row.id
            this.addNow = true
            this.formTitle = {
                name:row.name
            }
        },
        pageSizeChangeFn(val){
            // console.log(val)
            this.page_size = val
            this.init()
        },
        currentPageChangeFn(val){
            this.page = val
            this.init()
        },
        deleteFn(row){
             this.$confirm('', '', {
                confirmButtonText: '',
                cancelButtonText: '',
                type: 'warning'
            }).then(() => {
                let conf = {
                    url : '/api/api_backend.php?r=system-setting/area-del',
                    data : {
                        id:row.id
                    },
                    success:(data)=>{
                        if( data.statusCode == 1 ){
                            this.init()
                            Message({
                                message: data.message,
                                type: 'success',
                                duration: 3 * 1000
                            })
                        }else if(data.statusCode == 0){
                            Message({
                                message: data.message,
                                type: 'erro',
                                duration: 3 * 1000
                            })
                        }
                    }
                }
                axiosRequest(conf)
            }).catch( () =>{
                Message({
                    message:'',
                    type: 'erro',
                    duration: 3 * 1000
                })
            })
        }
    }
}
</script>
<style>
.blueB{background-color:rgba(0, 204, 255, 0.0980392156862745);padding:10px 0;margin:10px 0;}
.marginU{margin-top:10px;}
.el-form-item{margin-bottom:12px;}
.el-form-item__content,.el-form-item__label{line-height:30px;}
.el-input__inner{height:30px;line-height:30px;}
.dialog-footer{text-align:right;}
.totalT{width:98%;font-size:14px;line-height:23px;color: #909399;border:1px solid grey;}
</style>






